import { Center, Text, Actionsheet, Heading, Button, VStack, Box, Icon, useDisclose } from 'native-base'
import { ImageBackground, StyleSheet } from 'react-native'
import { MaterialIcons } from '@expo/vector-icons'
import { useNavigation } from '@react-navigation/native'
import { useEffect } from 'react';

export function Welcome() {
   const { navigate } = useNavigation();
   const { onClose, isOpen, onOpen} = useDisclose();
   
   function handleNavigateForProfile() {
      navigate('profile', { id: '0fee3f34-fb02-4f7a-99f4-f46e0dc6e225' })
   }

   useEffect(() => {
      onOpen();
   }, [])

   return (
      <Center flex={1} bg="gray.900" position="static">
         <ImageBackground 
           source={require('../../assets/bg.jpg')}
           style={styles.backgroundImage}
         >
            <Actionsheet isOpen={isOpen} size='full' hideDragIndicator onClose={onClose}>
               <Actionsheet.Content bg="gray.900" height='sm'>
                  <VStack w="full" h="full" pt={16} py={8} px={5} justifyContent='space-between'>
                     <Box>
                        <Text 
                           color="green.600" 
                           fontFamily="heading" 
                           fontSize="lg"
                           textAlign="center"
                           mb={2}
                        >
                           Em busca de um Profissional? Está no lugar certo!
                        </Text>
                        <Text 
                           color="gray.500" 
                           textAlign="center"
                           fontSize="sm"
                           fontFamily="body"
                           width="full"
                        >
                           Precisando de um serviço? Não perca tempo, acesse e encontre profissionais 
                           de diferentes áreas e lugares.
                        </Text>
                     </Box>
                     <Button 
                        bg="green.600"
                        rounded="full"
                        height={14}
                        _pressed={{
                           bg: "green.700"
                        }}
                        onPress={() => {handleNavigateForProfile(), onClose()} }
                     > 
                        <Text fontFamily="heading" color="white">Acessar agora</Text>        
                     </Button>
                  </VStack>
               </Actionsheet.Content>
                  <Box 
                     position="absolute" 
                     w={16} h={16} 
                     bg="green.600" 
                     alignItems="center" 
                     justifyContent="center"
                     rounded="full"
                     bottom={350}
                  >
                     <Icon as={MaterialIcons} name="engineering" color="white" size="2xl"/>
                  </Box>
               </Actionsheet>
         </ImageBackground>
      </Center>
   )
}

const styles = StyleSheet.create({
   backgroundImage: {
      flex: 1,
      justifyContent: 'center',
      alignItems: 'center',
      resizeMode: 'cover',
      width: '100%',
      height: '100%',
      position: "relative"
   }
})