import { Button as ButtonNativeBase, IButtonProps, Text } from 'native-base'

interface ButtonProps extends IButtonProps {
   text: string;
   variant?: "PRIMARY" | "SECONDARY"
   onFunctionCallback?: () => void;
}

export function Button({ text, onFunctionCallback, variant = "PRIMARY", ...rest }: ButtonProps) {
   return (
      <ButtonNativeBase { ...rest }
         bg={variant === 'PRIMARY' ? 'green.600' : 'gray.500'}
         alignItems="center"
         justifyContent="center"
         rounded="full"
         p={4}
         flex={1}
         _pressed={{
            bg: variant === 'PRIMARY' ? 'green.700' : 'gray.600'
         }}
      >
         <Text 
            fontSize="md" 
            fontFamily="heading"
            color='white'
         >
           {text}
         </Text>
      </ButtonNativeBase>
   )
}