import { Box, HStack, Icon, Text, VStack } from "native-base";
import { MaterialIcons } from '@expo/vector-icons'

interface AtendancesProps {
   locais_atendimento: string;
}

export function Atendances({ locais_atendimento }: AtendancesProps) {
   return (
      <VStack mb={12}>
         <HStack mt={4} mb={1} space={1} alignItems="center">
            <Icon as={MaterialIcons} name="location-on" size="md" color="green.700"/>
            <Text color="green.700" fontSize="md" fontFamily="heading">Atendimentos</Text>
         </HStack>
         <Text color="gray.500" fontSize={13}>{ locais_atendimento }</Text>
      </VStack>
   )
}