import { Entypo, Feather, FontAwesome } from '@expo/vector-icons';
import { useRoute } from '@react-navigation/native';
import { Actionsheet, Box, Center, HStack, Icon, Text, VStack } from 'native-base';
import { useEffect, useState } from 'react';
import { ImageBackground, Linking, TouchableOpacity } from 'react-native';

import { ApiResponseProfile, ProfessionalProfile } from '../../@types/Professional';
import { api } from '../../services/api';
import { styles } from '../../styles/backgroundImage';
import { Button } from '../Button';
import { Loading } from '../Loading';
import { AreasExpertise } from './AreasExpertise';
import { Atendances } from './Atendances';

type ParamsProps = {
   id: string;
}

export function Profile() {
   const route = useRoute();
   const { id } = route.params as ParamsProps;
   const [professional, setProfessional] = useState<ProfessionalProfile | null>();

   async function getData() {
      const response = await api.get<ApiResponseProfile>(id)
      if(response.data.meta.results)
         setProfessional(response.data.meta.results);
   }

   useEffect(() => {
     getData().then(() => console.log(''));
   }, [id]) 

   if (!professional) return <Loading />
   
   return (
      <Center flex={1} bg="gray.900">
         <ImageBackground 
           source={require('../../../assets/bg-profile.jpg')}
           style={styles.backgroundImage}
         >
            <Actionsheet isOpen={true}>
               <Actionsheet.Content bg="gray.900" h="xl">
                  <VStack w="full" h="full" py={6} px={5} justifyContent="space-between">
                     <VStack>
                        <HStack justifyContent="space-between">
                           <Icon as={Feather} name="code" size="md" color="green.600"/>
                           <HStack space={2} alignItems="center">
                              <Icon as={FontAwesome} name="star" size="sm" color="white"/>
                              <Text color="white" mr={2}>Novo</Text>
                              <TouchableOpacity>
                                 <Icon as={Entypo} name="share" size="md" color="white"/>
                              </TouchableOpacity>
                           </HStack>
                        </HStack>

                        <Box mt={3}>
                           <Text color="green.300" fontFamily="heading">
                              { professional?.area.nome}
                           </Text>
                           <Text color="white" fontFamily="heading" fontSize={18}>
                              { professional?.nome_completo }
                           </Text>
                        </Box>
                        
                        <AreasExpertise atuacao={ professional.atuacoes }/>
                        <Atendances locais_atendimento={ professional.locais_atendimento }/>
                     </VStack>

                     <VStack>
                        <HStack space={4} mb={6}>
                           <Button
                              text='Telefone'
                              variant='SECONDARY'
                              leftIcon={ <Icon as={FontAwesome} name="phone" size="md" color="white"/> }
                              onPress={ async () => await Linking.openURL(`tel:${professional.telefone}`) }
                           />
                           <Button
                              text='Whatsapp'
                              leftIcon={ <Icon as={FontAwesome} name="whatsapp" size="md" color="white"/> }
                              onPress={ async () => await Linking.openURL(`whatsapp://send?text=Olá&phone=+55${professional.zap}`) }
                           />
                        </HStack>  

                        <TouchableOpacity>
                           <Text color="green.600" textAlign="center" fontSize={13}>
                              Quer saber mais sobre o Serverino? clique aqui.
                           </Text>
                        </TouchableOpacity>
                     </VStack>
                     
                  </VStack>

               </Actionsheet.Content>
            </Actionsheet>
         </ImageBackground>
      </Center>
   )
}
