import { HStack, VStack, Icon, Text, FlatList, ScrollView } from "native-base";
import { Feather } from '@expo/vector-icons'
import { Area } from "./Area";

interface AreasExpertiseProps {
   atuacao: {
      id: number;
      titulo: string;
      descricao: string;
   }[]
}

export function AreasExpertise({ atuacao }: AreasExpertiseProps) {
   return (
      <VStack borderBottomWidth={1} borderBottomColor="gray.600" pb={6}>
         <HStack mt={8} mb={1} space={2}>
            <Icon as={Feather} name="list" size="md" color="green.700"/>
            <Text color="green.700" fontSize="md" fontFamily="heading">Áreas de atuação</Text>
         </HStack>
         <ScrollView h={120} showsVerticalScrollIndicator={false}>
            { atuacao.map(item => {
               return <Area name={item.titulo} description={item.descricao} key={item.titulo}/>
            })}
         </ScrollView>
      </VStack>
   )
}