import { Box, Text } from "native-base"

interface AreaProps {
   name: string;
   description: string;
}

export function Area({ name, description }: AreaProps) {
   return (
      <Box mt={2}>
         <Text color="white" fontSize="md" fontFamily="heading">{name}</Text>
         <Text color="gray.500" fontFamily="body" fontSize={13}>{description}</Text>
      </Box>
   )
}