import { NavigationContainer } from '@react-navigation/native'
import { StackRoutes } from './stack.routes'

const linking = {
   prefixes: ['http://localhost:19006/--/', 'exp://192.168.1.105:19000/--/'],
   config: {
      screens: {
         welcome: {
            path: 'welcome',
         },
         profile: {
            path: 'profile/:id',
            parse: {
               id: (id: string) => `${id}`,
            },
         },
      },
   }
};

export function Routes() {
   return (
      <NavigationContainer linking={linking}>
         <StackRoutes />
      </NavigationContainer>
   )
}